<template>
  <div>
    <LayoutBox one="视频管理">
      <el-form :model="form" size="small" inline>
        <el-form-item>
          <router-link to="/videoSet"><el-button icon="el-icon-setting" type="success">热门配置</el-button></router-link>
        </el-form-item>
        <el-form-item label="视频名称">
          <el-input v-model="form.videoName"></el-input>
        </el-form-item>
        <el-form-item label="作者">
          <el-input v-model="form.userName"></el-input>
        </el-form-item>
        <el-form-item label="审核状态">
          <el-select v-model="form.approvalStatus" placeholder="请选择">
            <el-option label="全部" value="" />
            <el-option label="未审核" value="0" />
            <el-option label="已通过" value="1" />
            <el-option label="已拒绝" value="2" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="apiGetVideoList">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="videoList" :default-sort="{ prop: 'createTime', order: 'descending' }" style="width: 100%;" height="600px" size="mini" @sort-change="onSort">
        <el-table-column prop="createTime" label="上传时间" align="center" sortable="custom" width="140"></el-table-column>
        <el-table-column prop="videoName" label="视频名称" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column label="视频" width="150" align="center">
          <template slot-scope="scope">
            <video :src="scope.row.videoUrl" preload="auto" controls width="150" height="130" />
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="作者" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column label="置顶状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.isTop === 1">置顶</span>
          </template>
        </el-table-column>
        <el-table-column label="审核状态" align="center">
          <template slot-scope="scope">
            <span v-if="!scope.row.approvalStatus">未审核</span>
            <span v-if="scope.row.approvalStatus === 1">已通过</span>
            <span v-if="scope.row.approvalStatus === 2">拒绝审核</span>
          </template>
        </el-table-column>
        <el-table-column prop="likeNum" label="获得点赞数" align="center" sortable="custom" width="105"></el-table-column>
        <el-table-column prop="realLikeNum" label="真实获得点赞数" align="center" sortable="custom" width="130"></el-table-column>
        <el-table-column label="浏览量" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.traffic }}</p>
            <el-button size="mini" @click="onSet(scope)">设置</el-button>
          </template>
        </el-table-column>
        <el-table-column label="增加点赞数量" width="250" align="center">
          <template slot-scope="scope">
            <div class="btn_edit_group">
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 100)">+100</el-button>
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 1000)">+1000</el-button>
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 10000)">+1w</el-button>
            </div>
            <div style="margin-top: 5px">
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 100000)">+10W</el-button>
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 1000000)">+100w</el-button>
              <el-button type="primary" size="mini" style="width: 65px;" @click="onEdit(scope, 10000000)">+1000W</el-button>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="90">
          <template slot-scope="scope">
            <div>
              <el-button type="text" @click="onDetele(scope)">删除</el-button>
              <el-button v-if="scope.row.enableStatus === 1" type="text" @click="onDisable(scope)">禁用</el-button>
              <el-button v-else type="text" @click="onEnable(scope)">启用</el-button>
              <el-button v-if="scope.row.isTop" type="text" @click="onCancelTop(scope)">取消置顶</el-button>
              <el-button v-else type="text" @click="onTop(scope)">置顶</el-button>
            </div>
            <div v-if="!scope.row.approvalStatus">
              <el-button type="text" style="color: #22ce5e" @click="onApprovalStatus(scope, 1)">通过</el-button>
              <el-button type="text" style="color: red" @click="onApprovalStatus(scope, 2)">拒绝</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </LayoutBox>
    <el-dialog :visible.sync="dialogVisible" width="30%">
      <span>这是一段信息</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>

    <traffic-cmp :dialogVisible="trafficVisible" :selectVideoId="selectVideoId" @close="trafficClose" />
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import TrafficCmp from './cmp/traffic.vue'

export default {
  name: 'video_list',

  components: {
    LayoutBox,
    TrafficCmp
  },

  data () {
    return {
      total: 0, // 分页总条数
      queryInit: {
        pageNo: 1,
        pageSize: 10,
      }, // 分页初始化
      videoList: [], // 视频列表
      form: {
        orderByType: 0,
      }, // 搜索用的表单
      dialogVisible: false, // 修改点赞数量

      // -------------------------------------------------
      trafficVisible: false, // 修改浏览量的窗口
      selectVideoId: '', // 临时保存选择的视频id
    }
  },

  methods: {
    // 分页选择一页显示多少数量
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
      this.apiGetVideoList()
    },
    // 分页选择第几页
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
      this.apiGetVideoList()
    },
    // 点击删除
    onDetele (scope) {
      this.apiDeleteVideo(scope)
    },
    // 禁用 0-禁用 1-启用
    onDisable (scope) {
      this.apiEnableOrdisable(scope, 0)
    },
    // 启用
    onEnable (scope) {
      this.apiEnableOrdisable(scope, 1)
    },
    // 审核
    onApprovalStatus (scope, approveStatus) {
      // 审批状态 0-未审批 1-已审批 2-驳回
      switch (approveStatus) {
        case (1): {
          this._confirm('审核通过').then(() => this.apiVideoApprove(scope, approveStatus))
          break
        }
        case (2): {
          this._confirm('审核拒绝').then(() => this.apiVideoApprove(scope, approveStatus))
          break
        }
      }
    },
    // 点击修改点赞数
    onEdit (scope, count) {
      this.$confirm(`增加${count}点赞数量, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'success'
      }).then(() => {
        this.apiUpdateLike(scope, scope.row.likeNum + count)
      })
    },
    // 表格排序
    onSort ({ column, prop, order }) {
      switch (order) {
        case 'ascending': { // 正序
          this.form.orderByType = this._sortKeyAscending(prop)
          break
        }
        case 'descending': { // 倒序
          this.form.orderByType = this._sortKeyDescending(prop)
          break
        }
        default: {
          break
        }
      }
      this.apiGetVideoList()
    },
    // 设置置顶
    onTop (scope) {
      this.apiUpdateTop(scope.row.id, 1)
    },
    // 取消置顶
    onCancelTop (scope) {
      this.apiUpdateTop(scope.row.id, 0)
    },
    // 关闭修改浏览量
    trafficClose () {
      this.trafficVisible = false
      this.apiGetVideoList()
    },
    // 点击设置浏览量
    onSet (scope) {
      this.trafficVisible = true
      this.selectVideoId = scope.row.id
    },

    // 私有方法-----------------------------------------------
    _confirm (msg) {
      return this.$confirm(`${msg}, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then()
    },
    // 判断是哪个字段需要排序(正序)
    _sortKeyAscending (prop) {
      switch (prop) {
        case 'likeNum': { // 全部的赞
          return 8
        }
        case 'realLikeNum': { // 真实的赞
          return 7
        }
        case 'createTime': { // 上传时间
          return 1
        }
        default:
          return ''
      }
    },
    // 判断是哪个字段需要排序(倒序)
    _sortKeyDescending (prop) {
      switch (prop) {
        case 'likeNum': { // 全部的赞
          return 10
        }
        case 'realLikeNum': { // 真实的赞
          return 9
        }
        case 'createTime': { // 上传时间
          return 0
        }
        default:
          return ''
      }
    },
    // 分页查询视频列表
    apiGetVideoList () {
      let data = {
        ...this.queryInit,
        ...this.form
      }
      this.$http.post('/woho/homepage/video/query', data)
        .then((res) => {
          if (res.data.success) {
            this.videoList = res.data.data.list
            this.total = res.data.data.total
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 删除
    apiDeleteVideo (scope) {
      this.$http.delete(`/woho/homepage/video/delete/${scope.row.id}`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('删除成功')
            this.videoList.splice(scope.$index, 1)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 用户 启用/禁用
    apiEnableOrdisable (scope, enableStatus) {
      this.$http.post(`/woho/homepage/video/enableOrdisable/${scope.row.id}/${enableStatus}`)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('操作成功')
            scope.row.enableStatus = enableStatus
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 视频审批
    apiVideoApprove (scope, approveStatus) {
      let data = {
        id: scope.row.id,
        approveStatus: approveStatus
      }
      this.$http.post('/woho/homepage/video/approve', data)
        .then((res) => {
          if (res.data.success) {
            scope.row.approvalStatus = approveStatus
            this.$message.success('操作成功')
          } else { this.$message.error(res.data.msg) }
        })
    },
    // 修改点赞数
    apiUpdateLike (scope, likeNum) {
      let data = {
        id: scope.row.id,
        likeNum,
      }
      this.$http.post('/woho/homepage/video/updateLike', data)
        .then((res) => {
          console.log('修改点赞', res)
          if (res.data.success) {
            this.$message.success('修改成功')
            scope.row.likeNum = likeNum
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 置顶和取消置顶
    async apiUpdateTop (videoId, status) {
      const res = await this.$http.get(`/woho/homepage/video/updateTop/${videoId}/${status}`)
      if (res.data.success) {
        this.apiGetVideoList()
        this.$message.success('操作成功')
      } else {
        this.$message.error(res.data.msg)
      }
    }
  },

  created () {
    this.apiGetVideoList()
  }
}
</script>

<style lang="scss" scoped>

</style>
