<template>
  <el-dialog title="浏览量" :visible.sync="dialogVisible" :close-on-click-modal="false" :before-close="onClose" width="30%">
    <el-input v-model.number="number"></el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="onClose">取 消</el-button>
      <el-button type="primary" @click="onSave">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'trafficCmp',

  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    },
    selectVideoId: {
      type: String,
      default: '',
    }
  },

  data () {
    return {
      number: '',
    }
  },

  methods: {
    onClose () {
      this.$emit('close')
    },
    async onSave () {
      if (this.number > -1) {
        const res = await this.$http.get(`${this.$api.updateTraffic}/${this.selectVideoId}/${this.number}`)
        if (res.data.success) {
          this.$message.success('保存成功')
          this.onClose()
        } else {
          this.$message.error(res.data.msg)
        }
      } else {
        this.$message.error('请输出正确的浏览量')
      }
    }
  }
}
</script>

